.animation1{
    -webkit-animation: moveRigth 1.5s infinite linear;
            animation: moveRigth 1.5s infinite linear;
}
@-webkit-keyframes moveRigth{
    from{
        -webkit-transform: translateX(0);
                transform: translateX(0);
        box-shadow: 0px 0px 0px 0px transparent;
        background-color: transparent;
    }
    to{
        -webkit-transform: translateX(5px);
                transform: translateX(5px);
        box-shadow: -7px 0px 0px 0px transparent;
        background-color: transparent;
        
    }
}
@keyframes moveRigth{
    from{
        -webkit-transform: translateX(0);
                transform: translateX(0);
        box-shadow: 0px 0px 0px 0px transparent;
        background-color: transparent;
    }
    to{
        -webkit-transform: translateX(5px);
                transform: translateX(5px);
        box-shadow: -7px 0px 0px 0px transparent;
        background-color: transparent;
        
    }
}

.animation2{
    -webkit-animation: colors 2s infinite ease;
            animation: colors 2s infinite ease;
}

@-webkit-keyframes colors{
    0%{
        background-color: #15dbff;
    }
    50%{
        background-color: #5c2da0;
    }
    100%{
        background-color: #f1d87d;
    }
}

@keyframes colors{
    0%{
        background-color: #15dbff;
    }
    50%{
        background-color: #5c2da0;
    }
    100%{
        background-color: #f1d87d;
    }
}

.animation-carrito{
    -webkit-animation: carrito 12s infinite ease;
            animation: carrito 12s infinite ease;
}

@-webkit-keyframes carrito{
    from{
        -webkit-transform: translateX(-150%);
                transform: translateX(-150%);
    }
    to{
        -webkit-transform: translateX(1%);
                transform: translateX(1%);
    }
}

@keyframes carrito{
    from{
        -webkit-transform: translateX(-150%);
                transform: translateX(-150%);
    }
    to{
        -webkit-transform: translateX(1%);
                transform: translateX(1%);
    }
}

